import React from 'react';
import { Buffer } from 'buffer';

const zlib = require('pako');

function NewlineText(props) {
  const text = props.text;
  return <div style={{'whiteSpace': 'pre-wrap'}}>{text}</div>;
}

function FaqUl(props) {
  return (
    <ul className="text-left faq-ul">
      {props.children}
    </ul>
  );
}

const EctlCommon = {};

// try to keep in alphabetical
// order if possible, thanks
const vanguardTypeArr = [
  "Destroyer",
  "Guided-missile Destroyer",
  "Heavy Cruiser",
  "Large Cruiser",
  "Light Cruiser",
  "Munition Ship",
  "Sailing Frigate (Vanguard)",
];

const mainTypeArr = [
  "Aircraft Carrier",
  "Aviation Battleship",
  "Battlecruiser",
  "Battleship",
  "Guided-missile Destroyer",
  "Light Aircraft Carrier",
  "Monitor",
  "Repair Ship",
  "Sailing Frigate (Main)",
];

const subTypeArr = [
  "Sailing Frigate (Submarine)",
  "Submarine",
  "Submarine Carrier",
];

EctlCommon.AllHullTypes = {
  "vanguard": vanguardTypeArr,
  "main": mainTypeArr,
  "sub" : subTypeArr,
};

const typeMapping = {
  "DD"  : "Destroyer",
  "DDG" : "Guided-missile Destroyer",
  "CL"  : "Light Cruiser",
  "CA"  : "Heavy Cruiser",
  "CB"  : "Large Cruiser",
  "IXv" : "Sailing Frigate (Vanguard)",
  "AE"  : "Munition Ship",
  "BB"  : "Battleship",
  "BC"  : "Battlecruiser",
  "BBV" : "Aviation Battleship",
  "BM"  : "Monitor",
  "IXm" : "Sailing Frigate (Main)",
  "CV"  : "Aircraft Carrier",
  "CVL" : "Light Aircraft Carrier",
  "AR"  : "Repair Ship",
  "SS"  : "Submarine",
  "SSV" : "Submarine Carrier",
  "IXs" : "Sailing Frigate (Submarine)",
};

EctlCommon.canonicalizeEquipmentType = (type) => {
  if (!type){
    return 'Auxiliary';
  }
  if (type.startsWith('DD'))
    return 'DD Gun';
  if (type.startsWith('CL'))
    return 'CL Gun';
  if (type.startsWith('CA'))
    return 'CA Gun';
  if (type.startsWith('CB'))
    return 'CB Gun';
  if (type.startsWith('BB'))
    return 'BB Gun';
  if (type.match(/^anti[\s-]*air|^AA/i))
    return 'AA Gun';
  if (type.match(/^fighter/i))
    return 'Fighter';
  if (type.match(/^dive\s?bomber/i))
    return 'Dive Bomber';
  if (type.match(/^torp(edo)?\s*bomber/i))
    return 'Torpedo Bomber';
  if (type.match(/^sub(marine)?\s*torp(edo)?(e?s)?/i))
    return 'Submarine Torpedo';
  if (type.match(/^sea\s*plane/i))
    return 'Seaplane';
  if (type.match(/^torp(edo)?(e?s)?/i))
    return 'Torpedo';
  if (type.match(/^cargo/i))
    return 'Cargo';
  if (type.match(/^sonar/i))
    return 'Sonar';
  if (type.match(/^depth\s*charge/i))
    return 'Depth Charge';
  if (type.match(/^asw\s*bomber/i))
    return 'ASW Bomber';
  if (type.match(/^asw\s*(heli)?[\s-]*copter/i))
    return 'ASW Helicopter';
  if (type.match(/^(guided?)?[\s-]*missile/i))
    return 'Missile';
  if (type.match(/^Augment/i))
    return 'Augment';
  return 'Auxiliary';
};

const simplifiedHullMapping = {
  "DD"  : "dd",
  "DDG" : "dd",
  "CL"  : "cl",
  "CA"  : "ca",
  "CB"  : "ca",
  "IXV" : "ixv",
  "AE"  : "ae",
  "BB"  : "bb",
  "BC"  : "bb",
  "BBV" : "bb",
  "BM"  : "bm",
  "IXM" : "ixm",
  "CV"  : "cv",
  "CVL" : "cv",
  "AR"  : "ar",
  "SS"  : "ss",
  "SSV" : "ss",
  "IXS" : "ixs",
};

EctlCommon.getSimplifiedHullCode = (str) => {
  return simplifiedHullMapping[str.toUpperCase()];
};

EctlCommon.AllHullCodes = Object.getOwnPropertyNames(typeMapping);
const inverseTypeMapping = Object.getOwnPropertyNames(typeMapping).reduce((acc, hull_code) => { acc[typeMapping[hull_code].toLowerCase()] = hull_code; return acc; }, {});

EctlCommon.getHullCodeFromStr = (str) => {
  return inverseTypeMapping[str.toLowerCase()];
};

EctlCommon.getStrFromHullCode = (str) => {
  return typeMapping[str.toUpperCase()];
};

EctlCommon.shallowCloneObject = (x) => Object.assign({}, x);

EctlCommon.deduplicateArray = (obj, propName = null) => {
  let seen = {};
  const transform = propName ? ((x) => x[propName]) : ((x) => x);
  return obj.filter((item) => {
    const item_tr = transform(item);
    return seen.hasOwnProperty(item_tr) ? false : (seen[item_tr] = true);
  });
};

function ConditionalWrapper({condition, wrapper, children}) {
  return condition ? wrapper(children) : children;
}

function charFor(hextet){
  if (hextet < 26){
    return String.fromCodePoint(hextet + 'A'.codePointAt(0));
  } else if (hextet < 52){
    return String.fromCodePoint(hextet - 26 + 'a'.codePointAt(0));
  } else if (hextet < 62){
    return String.fromCodePoint(hextet - 52 + '0'.codePointAt(0));
  } else if (hextet === 62){
    return '-';
  } else if (hextet === 63){
    return '_';
  } else {
    return '=';
  }
}

const hextetLUT = new Array(64);
const hextetReverseLUT = {};

for (let i = 0; i < 64; i++){
  const c = charFor(i);
  hextetLUT[i] = c;
  hextetReverseLUT[c] = i;
}
hextetReverseLUT['='] = 0;

/**
 * ArrayBuffer input
 */
EctlCommon.encodeBase64 = (buffer) => {
  const view = new DataView(buffer);
  const chars = new Array(4 * Math.ceil(buffer.byteLength / 3));
  const decodeTo = (slot, value) => {
    for (let o = 0; o < 4; o++){
      chars[slot + o] = hextetLUT[(value >> (18 - 6*o)) & 0x3F];
    }
  };
  let offset;
  for (offset = 0; offset + 3 <= buffer.byteLength; offset += 3){
    const value = (view.getUint8(offset) << 16) | view.getUint16(offset + 1, false);
    decodeTo(offset / 3 * 4, value);
  }
  switch (buffer.byteLength % 3){
    case 2:
      decodeTo(offset / 3 * 4, view.getUint16(offset, false) << 8);
      chars[offset / 3 * 4 + 3] = '=';
      break;
    case 1:
      decodeTo(offset / 3 * 4, view.getUint8(offset) << 16);
      chars[offset / 3 * 4 + 2] = '=';
      chars[offset / 3 * 4 + 3] = '=';
      break;
    default:
     break;
  }
  return chars.join('');
};

EctlCommon.decodeBase64 = (string) => {
  if (string.length % 4 !== 0){
    throw RangeError('base64 strings are always multiple of 4 length');
  }
  if (string === ''){
    return new ArrayBuffer(0);
  }
  const buf = new ArrayBuffer(string.length / 4 * 3);
  const view = new DataView(buf);
  const toValue = (str) => {
    const value = ((hextetReverseLUT[str.charAt(0)] << 18)
      + (hextetReverseLUT[str.charAt(1)] << 12)
      + (hextetReverseLUT[str.charAt(2)] << 6)
      + (hextetReverseLUT[str.charAt(3)]));
    return value;
  };
  let offset;
  for (offset = 0; offset < string.length; offset += 4){
    const str = string.slice(offset, offset + 4);
    const value = toValue(str);
    view.setUint8(offset / 4 * 3, (value >> 16) & 0xFF);
    view.setUint16(offset / 4 * 3 + 1, value & 0xFFFF, false);
  }
  let x = 0;
  while (string.charAt(x - 1) === '='){
    x--;
  }
  if (x < 0){
    return buf.slice(0, x);
  } else {
    return buf;
  }
};

EctlCommon.compress = (buffer) => {
  return Buffer.from(zlib.deflateRaw(Buffer.from(buffer))).toString('base64');
};

EctlCommon.decompress = (inputStr) => {
  return zlib.inflateRaw(Buffer.from(inputStr, 'base64')).buffer;
};

export { EctlCommon, FaqUl, NewlineText, ConditionalWrapper };
